import request from '@/utils/request'

/**
 * 文章提交接口
 * @param {*} data
 *
 */
export function essaySubmit1API(data) {
    return request({
        url: `feedbackWithUserId`,
        method: 'post',
        data: data,
    })
}

/**
 * 文章提交接口2
 * @param {*} data
 *
 */
export function essaySubmit2API(data) {
    return request({
        url: `feedbackWithUserIdv2`,
        method: 'post',
        data: data,
    })
}


